import * as styles from "../styles/index.module.css";
import React, { useState, useEffect } from "react";
import { useSelector } from 'react-redux'

const IndexPage = () => {
    return (
        <main>
            <div className={styles.container}>
                {/* Title Section */}
                <div className={styles.center}>
                    <CenteredSVG
                        label="records"
                        href="/records"
                        cursorColor="#FFD700" // gold
                    />
                </div>
                <div className={styles.center}>
                    <CenteredSVG
                        label="homes"
                        href="/homes"
                        cursorColor="#39FF14" // green
                    />
                </div>
                <div className={styles.center}>
                    <CenteredSVG
                        label="sites"
                        href="/sites"
                        cursorColor="#9201C5" // purple
                    />
                </div>
                <div className={styles.center}>
                    <CenteredSVG
                        label="shops"
                        href="/shop"
                        cursorColor="#FF69B4" // purple
                    />
                </div>
            </div>
        </main>
    );
};

const CenteredSVG = ({ label, href, cursorColor }) => {
    const accountName = useSelector((state) => state.account.accountInfo.name);
    const [blinkSpeed, setBlinkSpeed] = useState(getRandomBlinkSpeed());

    // Function to generate a random blink speed between 300ms and 1.5s
    function getRandomBlinkSpeed() {
        return Math.random() * (1500 - 300) + 300; // Random value between 300ms and 1500ms
    }

    useEffect(() => {
        const interval = setInterval(() => {
            setBlinkSpeed(getRandomBlinkSpeed()); // Update blink speed at random intervals
        }, 5000); // Update blink speed every 5 seconds

        return () => clearInterval(interval); // Cleanup on unmount
    }, []);

    return (
        <svg
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 1200 300" // Adjusted to fit larger content
            width="100%"
            height="auto"
            className={styles.responsiveSvg}
        >
            {/* Group to center all elements */}
            <g transform="translate(600, 150)" textAnchor="middle">
                {/* "walkum" Text */}
                <text
                    x="-300" // Offset to the left
                    fill="white"
                    fontFamily="Courier"
                    fontSize="90px"
                >
                    {accountName}
                </text>
                {/* Blinking Cursor */}
                <text
                    x="0"
                    fill={cursorColor}
                    fontFamily="Courier"
                    fontSize="90px"
                    fontWeight="bold"
                    className={styles.blinkingCursor}
                    style={{
                        animationDuration: `${blinkSpeed}ms`, // Apply random speed
                        transform: "scale(1, 5)", // Scale 1.5 times taller
                    }}
                >
                    |
                </text>
                {/* Clickable Label */}
                <a href={href}>
                    <text
                        x="300" // Offset to the right
                        className={styles.svgButton}
                        fontFamily="Courier"
                        fontSize="90px"
                    >
                        {label}
                    </text>
                </a>
            </g>
        </svg>
    );
};

export default IndexPage;

export const Head = () => <title>Home Page</title>;
